import { Component } from '@angular/core';
import Quagga from 'quagga';
import { AlertController } from 'ionic-angular';
import { BarcodeService } from '../../services/barcode.service';
import { BarcodeDecodeOutput } from '../../store/interfaces/barcode/barcode-decode-output';

/**
 * Generated class for the BarcodeReaderComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'barcode-reader',
  templateUrl: 'barcode-reader.html'
})
export class BarcodeReaderComponent {

  isBarcodeReaderStopped: boolean = false;
  quaggaOnDetectedHandler: Function;

  constructor(private alertCtrl: AlertController, public barcodeService: BarcodeService) {
    this.quaggaOnDetectedHandler = this.quaggaOnDetected.bind(this);
  }

  public startQuagga() {
    // check if hardware supports
    if(!navigator.mediaDevices || !(typeof navigator.mediaDevices.getUserMedia === 'function')) {
      this.isBarcodeReaderStopped = true;
      this.alert('This app can\'t use devices\' camera', 'Please make sure your device have camera and allow camera access.');
      return;
    }

    // viewport width
    const widthProportion = document.documentElement.clientWidth;
    // viewport height remove header-bar height = content height
    const heightProportion = document.documentElement.clientHeight - 51;

    // Streaming video for processing size
    const width: number = 1200;
    const height: number = width * (heightProportion / widthProportion);
     
    Quagga.init({
      inputStream: {
        name: "Live",
        type: "LiveStream",
        target: '#recordScreen',
        constraints: {
          width,
          height
        }
      },
      decoder: {
        readers: [ 'ean_reader' ]
      },
      locator: {
        halfSample: true,
        patchSize: 'medium'
      }
    }, (error) => {
      // is there something wrong ? console the error
      if(error) { console.log(error); this.isBarcodeReaderStopped = true; }
      // register event handler
      Quagga.onProcessed(this.quaggaOnProcessed);
      Quagga.onDetected(this.quaggaOnDetectedHandler);
      // Quagga.onDetected((data) => { this.alert('Barcode decoded', JSON.stringify(data)); });
      // start processing streaming video from camera
      Quagga.start();
      this.isBarcodeReaderStopped = false;
    });
  }

  public quaggaOnProcessed(result) {
    // variable declaration
    var drawingCtx = Quagga.canvas.ctx.overlay,
    drawingCanvas = Quagga.canvas.dom.overlay;
    // draw if there is a result
    if (result) {
        if (result.boxes) {
            drawingCtx.clearRect(0, 0, parseInt(drawingCanvas.getAttribute("width")), parseInt(drawingCanvas.getAttribute("height")));
            result.boxes.filter(function (box) {
              return box !== result.box;
            }).forEach(function (box) {
              Quagga.ImageDebug.drawPath(box, {x: 0, y: 1}, drawingCtx, {color: "green", lineWidth: 2});
            });
        }
        if (result.box) {
            Quagga.ImageDebug.drawPath(result.box, {x: 0, y: 1}, drawingCtx, {color: "#00F", lineWidth: 2});
        }
        if (result.codeResult && result.codeResult.code) {
            Quagga.ImageDebug.drawPath(result.line, {x: 'x', y: 'y'}, drawingCtx, {color: 'red', lineWidth: 3});
        }
    }
  }

  public quaggaOnDetected(data: BarcodeDecodeOutput) {
    // make sure what quagga detected was the successful result
    if(data && data.codeResult && data.codeResult.code) {
      this.barcodeService.barcodeDecoded(data);
    }
  }

  public stopQuagga():void {
    // stop processing steaming video from camera
    Quagga.stop();
    // remove event listener
    Quagga.offDetected(this.quaggaOnDetectedHandler);
    Quagga.offProcessed(this.quaggaOnProcessed);
    this.isBarcodeReaderStopped = true;
  }

  private alert(title: string, subTitle: string): void {
    const alert = this.alertCtrl.create({
      title,
      subTitle,
      buttons: ['OK']
    });
    alert.present();
  }

}



