import { Component, ViewChild } from '@angular/core';
import { select, Store } from '@ngrx/store';
import { NavController, Navbar } from 'ionic-angular';
import { Observable } from 'rxjs/Observable';
import { Subscription } from 'rxjs/Subscription';

import { BarcodeReaderComponent } from '../../components/barcode-reader/barcode-reader';
import { AppStore } from '../../store/interfaces/app-store';
import { BarcodeState } from '../../store/interfaces/barcode/barcode-state';
import { BarcodeDetailPage } from '../barcode-detail/barcode-detail';

@Component({
  selector: 'page-barcode-scanner',
  templateUrl: 'barcode-scanner.html',
})
export class BarcodeScannerPage {

  @ViewChild(Navbar) navBar: Navbar;
  @ViewChild(BarcodeReaderComponent) barcodeReader: BarcodeReaderComponent;
  private barcodeState: Observable<BarcodeState>;
  private barcodeStateSubscription: Subscription;

  constructor(
    private navCtrl: NavController,
    private store: Store<AppStore>
  ) {
    this.barcodeState = this.store.pipe(select('barcode'));
  }

  ionViewDidEnter() {
    // start quagga barcode reader
    this.barcodeReader.startQuagga();
    // if barcode decoded, send user to detial page
    this.barcodeStateSubscription = this.barcodeState.subscribe(data => {
      if(data.isDecoded) {
        this.navCtrl.push(BarcodeDetailPage);
        this.barcodeStateSubscription.unsubscribe();
      }
    });
    // animation on leave
    this.navBar.backButtonClick = () => {
      //Write here wherever you wanna do
      this.navCtrl.pop({ animation: 'ios-transition' })
    }
  }

  ionViewWillLeave() {
    this.barcodeReader.stopQuagga();
  }

}



